.box {
    height: auto;
    width: auto;
  }
  
  .box .group {
    height: 329px;
    left: 0;
    top: 0;
    width: 335px;
  }
  
  .box .overlap-group {
    background-color: #228800;
    border-radius: 4px;
    height: 329px;
    position: relative;
    width: 327px;
  }
  
  .box .vector {
    height: 178px;
    left: 3px;
    position: absolute;
    top: 0;
    width: 324px;
  }
  
  .box .rectangle {
    -webkit-backdrop-filter: blur(24px) brightness(100%);
    backdrop-filter: blur(24px) brightness(100%);
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0.1) 100%);
    border: 1px solid;
    border-color: transparent;
    border-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.2)) 1;
    border-radius: 16px 16px 0px 0px;
    height: 117px;
    left: 16px;
    position: absolute;
    top: 60px;
    width: 295px;
  }
  
  .box .clover-symbol-logo {
    background-color: #ffffff;
    border-radius: 14px/13.63px;
    height: 27px;
    left: 267px;
    position: absolute;
    top: 76px;
    width: 28px;
  }
  
  .box .img {
    height: 16px;
    left: 6px;
    position: absolute;
    top: 6px;
    width: 16px;
  }
  
  .box .frame {
    align-items: flex-start;
    display: inline-flex;
    flex-direction: column;
    height: 58px;
    left: 32px;
    overflow: hidden;
    position: absolute;
    top: 76px;
    font-size: 12px;
  }
  
  .box .text-wrapper {
    color: #004400;
